import React, { Component } from 'react';
import {URL_API} from '../App';
import Cookies from 'js-cookie';


/**
 * Class qui s'occupe de la gestion des carte tudiantes dans la page panel administrateur
 */
class TableauCarteId extends Component{
    constructor(props) {
      super(props);

      this.state = {
          tabID: this.props.Tableau,
          Actuel:{Prenom:"",Nom:"",Id:"",Extension:""}
      };
    }

    /**
     * Met  jour le tableau du component parent
     * Utilis lorsqu'on certifie/decline la carte 
     * d'un utilisateur
     */
    reset(){
        const axios = require('axios');  //Requtes HTTP
        const url = URL_API+'getTabCarteEtudiante.php';
        let config = {
            headers: {
            logginid: Cookies.get("ID"),
            logginkey: Cookies.get("KEY")
            }
        }
        axios.get(url,config)
        .then(res => {
            if(res.data.connected){ //Mise  jour de connected si rponse positive 
                this.props.updateTab(res.data.tab);
            }
        })
        .catch(err => {
            console.log(err);
        });
    }

    /**
     * Ouvre la carte tudiante d'un utilisateur dans un nouvel onglet
     */
    openCarte(){
        window.open("https://projetsiteeisti.yj.fr/imageCarteEtudiante/"+this.state.Actuel.Id+"."+this.state.Actuel.Extension);
    }

    /**
     * Certifie l'utilisateur slectionn
     */
    certificate(){
        const axios = require('axios');  //Requtes HTTP
        const url = URL_API+'setCertif.php?id='+this.state.Actuel.Id+"&img="+this.state.Actuel.Extension;
        let config = {
            headers: {
            logginid: Cookies.get("ID"),
            logginkey: Cookies.get("KEY")
            }
        }
        axios.get(url,config)
        .then(res => {
            console.log(res.data);
            if(res.data.connected){ //Mise  jour de connected si rponse positive 
                console.log("Profil certifi ! ");
                this.reset();
            }else{
                console.log("Erreur lors de la certification du profil.");
            }
        })
        .catch(err => {
            console.log(err);
        });
    }

    /**
     * Supprime la carte tudiante d'un utilisateur dans le but qu'il puisse 
     * en renvoyer une qui soit conforme
     */
    deleteCarte(){
        const axios = require('axios');  //Requtes HTTP
        const url = URL_API+'delCarteEtudiante.php?id='+this.state.Actuel.Id+"&img="+this.state.Actuel.Extension;
        let config = {
            headers: {
            logginid: Cookies.get("ID"),
            logginkey: Cookies.get("KEY")
            }
        }
        axios.get(url,config)
        .then(res => {
            console.log(res.data);
            if(res.data.connected){ //Mise  jour de connected si rponse positive 
                console.log("Carte tudiante supprime ! ");
                this.reset();
            }else{
                console.log("Erreur lors de la suppression de la carte tudiante.");
            }
        })
        .catch(err => {
            console.log(err);
        });
    }

    /**
     * Fonction qui actualise l'utilisateur courant slectionn
     * @param {Object} el objet contenant le prenom, le nom, 
     * l'id et l'extension de la carte tudiante d'un utilisateur
     */
    alertId(el){
        const axios = require('axios');  //Requtes HTTP
        const url = URL_API+'getExtensionCarte.php?id='+el.Id;
        let config = {
            headers: {
            logginid: Cookies.get("ID"),
            logginkey: Cookies.get("KEY")
            }
        }
        axios.get(url,config)
        .then(res => {
            if(res.data.connected){ //Mise  jour de connected si rponse positive 
                this.setState({
                    Actuel:{
                        Prenom:el.Prenom,
                        Nom:el.Nom,
                        Id:el.Id,
                        Extension:res.data.extension
                    }
                });
                console.log(el.Prenom+" "+el.Nom+" slectionn !");
            }
        })
        .catch(err => {
            console.log(err);
        });
    }

    /**
     * Transformation du tableau d'objet en un tableau html avec le nom, prenom et ID de l'utilisateur
     */
    PropsToTabHTML(){
    let res;
    if(this.props.Tableau!=null && this.props.Tableau.length>0){
        res = this.props.Tableau.map(el => <tr key={el.Id}><td>{el.Prenom}</td><td>{el.Nom}</td><td><button onClick={() => this.alertId(el)}>{el.Id}</button></td></tr>);
        return res;
    }else{
        return(<tr><td colSpan="3">Plus de carte  valider</td></tr>)
    }
    }


    /**
     * Rendu du component
     */
    render(){
      return(
        <div style={{marginTop:"20px"}}>
            <h2>Gestion des cartes tudiantes</h2>
            <div style={{display:"flex",justifyContent:"center",marginTop:"3%"}}>
                <div>
                <table style={{border:"thin solid black"}}>
                    <thead>
                        <tr>
                            <th>Prnom</th>
                            <th>Nom</th>
                            <th>ID</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.PropsToTabHTML()}
                    </tbody>
                </table>
                </div>
                {this.state.Actuel.Prenom!=="" &&
                <div style={{margin:"50px"}}>
                <button onClick={() => this.openCarte()}>Voir carte tudiante</button>
                <button style={{backgroundColor:"#48FF5B"}} onClick={() => this.certificate()}>Certifier</button>
                <button style={{backgroundColor:"#FF5B48"}} onClick={() => this.deleteCarte()}>Re-demander</button>
                </div>
                }
            </div>
        </div>
      );
    }
}

export default TableauCarteId;