import React, { Component } from 'react';
import TableauCarteId from './TableauCarteId';
import {URL_API} from '../App';
import Cookies from 'js-cookie';
import TableauManage from './TableauManage';
import { Redirect } from "react-router-dom";

/**
 * Class qui s'occupe de la page du panel administrateur
 */
class AdminCarte extends Component{
    constructor(props) {
      super(props);

      this.state = {
        allowed:true,
        loaded:false,
        array:null,
        buttonLabel:"Grer les comptes"
      };
    }

    /**
     * Component initialis
     */
    componentDidMount(){
      this.setPropsTableau();
    }

    /**
     * Fonction qui met  jour le tableau propos
     * Cette fonction sera pass en Props pour permettre aux components enfant d'acceder au this.state.array
     * @param {Tab of Objects} newArray Tableau renvoy par l'API contenant les informations des comptes 
     */
    updateTab = (newArray) => {
      this.setState({
        array: newArray
      });
    }

    /**
     * Met  jour un tat qui renverra l'utilisateur  une autre pas si il n'est pas connect
     * ou s'il n'a pas le grade administrateur
     */
    verifPermission(){
      const axios = require('axios');  //Requtes HTTP
      const url = URL_API+'isConnected.php';
      let config = {
          headers: {
          logginid: Cookies.get("ID"),
          logginkey: Cookies.get("KEY")
          }
      }
      axios.get(url,config)
      .then(res => {
          if(!(res.data.connect && res.data.grade==="administrateur")){ //Mise  jour de connected si rponse positive 
            this.setState({allowed:false});
          }
      })
      .catch(err => {
          console.log(err);
      });
    }

    /**
     * Initialise le tableau des comptes proposs
     * Initialisation pour la gestion des cartes
     */
    setPropsTableau(){
      const axios = require('axios');  //Requtes HTTP
      const url = URL_API+'getTabCarteEtudiante.php';
      let config = {
          headers: {
          logginid: Cookies.get("ID"),
          logginkey: Cookies.get("KEY")
          }
      }
      axios.get(url,config)
      .then(res => {
          if(res.data.connected){ //Mise  jour de connected si rponse positive 
            this.setState({
              array:res.data.tab,
              loaded:true
            });       
            this.verifPermission();
          }
      })
      .catch(err => {
          console.log(err);
      });
    }

    /**
     * Met  jour le tableau pour la gestion des comptes
     */
    setComtpeTableau(){
      const axios = require('axios');  //Requtes HTTP
      const url = URL_API+'getTabCarteEtudiante.php?account=yes';
      let config = {
          headers: {
          logginid: Cookies.get("ID"),
          logginkey: Cookies.get("KEY")
          }
      }
      axios.get(url,config)
      .then(res => {
          if(res.data.connected){ //Mise  jour de connected si rponse positive 
            this.setState({
              array:res.data.tab,
              loaded:true
            });
          }
      })
      .catch(err => {
          console.log(err);
      });
    }

    /**
     * Mise  jour des tableaux selon si l'on veut grer les cartes tudiantes 
     * ou s'il on veut grer les comptes 
     */
    buttonSwap(){
      if(this.state.buttonLabel==="Grer les comptes"){
        this.setState({
          buttonLabel: "Grer les certifications"
        });
        this.setComtpeTableau();
      }else{
        this.setState({
          buttonLabel: "Grer les comptes"
        });
        this.setPropsTableau();
      }
    }

    /**
     * Rendu du component
     */
    render(){
      if(!this.state.allowed){
        return(<Redirect to='/principale'/>);//si l'utilisateur n'as pas la permission il est renvoy  la page principale
      }
      return(
        <div style={{marginTop:"20px"}}>
          <h1>Panel administrateur</h1>
          <button onClick={() => this.buttonSwap()} style={{height:"70px",width:"200px"}}>{this.state.buttonLabel}</button>
          {(this.state.loaded && this.state.buttonLabel==="Grer les comptes") &&
          <TableauCarteId Tableau={this.state.array} updateTab={this.updateTab}/>
          }
          {(this.state.loaded && this.state.buttonLabel==="Grer les certifications") && 
          <TableauManage Tableau={this.state.array} updateTab={this.updateTab}/>
          }
        </div>
      );
    }
}

export default AdminCarte;